import React from 'react';
import { BrowserRouter, Switch, Route } from 'react-router-dom';

import mdcMainPage from './pages/mdcMainPage.js';

function App() {

  function mainPage() {
    return (
      <header className="App-header">
        <p>
          Edit <code>src/App.js</code> and save to reload.
        </p>
        <a
          className="App-link"
          href="https://reactjs.org"
          target="_blank"
          rel="noopener noreferrer"
        >
          Learn React
        </a>
      </header>
    )
  }

  return (
    <BrowserRouter>
      <Switch>
        <Route path='/' component={mainPage} exact />
        <Route path='/testprofile' component={mdcMainPage} />
      </Switch>
    </BrowserRouter>
  );
}

export default App;
